body{font-family:'Open Sans',sans-serif;font-size:18px;color:#010000;font-weight:400;font-style:normal;padding-bottom:20vh}.container-fluid{line-height:24px}footer,header{position:relative;display:block;width:100%;background:#010000;color:#fff;height:90px}footer .logo,header .logo{position:absolute;top:50%;left:15px;-webkit-transform:translateY(-50%);-moz-transform:translateY(-50%);transform:translateY(-50%)}footer nav,footer nav ul,footer nav ul li,header nav,header nav ul,header nav ul li{margin:0;padding:0}footer nav,header nav{position:absolute;top:50%;right:15px;-webkit-transform:translateY(-50%);-moz-transform:translateY(-50%);transform:translateY(-50%)}footer nav ul li,header nav ul li{list-style-type:none;display:inline-block;margin-right:20px}footer nav ul li.last,header nav ul li.last{margin:0}footer nav ul li a,header nav ul li a{font-size:19px;line-height:normal;color:#fff}footer a,header a{text-decoration:none;-webkit-transition:.4s ease-in-out;-moz-transition:.4s ease-in-out;transition:.4s ease-in-out;cursor:pointer}footer a:hover,header a:hover{color:#ff1755;-webkit-transition:.4s ease-in-out;-moz-transition:.4s ease-in-out;transition:.4s ease-in-out;text-decoration:none}footer{position:fixed;bottom:0}